//seccion raquel

.seccion-quienes-somos {
  margin: auto;
  padding: 0 20px 0 20px;
  box-sizing: border-box;
}

@media all and (min-width:1200px){

  .seccion-quienes-somos{
    width: 924px;
  }

}

.titulo-somos {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  text-align: center;
  color: #099D8D;
  margin: 30px 0 30px 0;
}

@media all and (min-width:768px){
  .titulo-somos{
    margin: 45px 0 45px 0;
  }
}

@media all and (min-width:768px){
  .wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr ;
  }

  .item1{
      grid-column-start: 1;
      grid-column-end: 1;
  }

  .item2{
        grid-column-start: 2;
        grid-column-end: 4;
  }

  .item4{
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 2;
  }

  .item6{
    grid-column-start: 2;
    grid-column-end: 4;
  }

  .item8{
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 4;
  }

  .item10{
    grid-column-start: 2;
    grid-column-end: 4;
  }

}

@media all and (min-width:1200px){

  .item4{
    display: flex;
    justify-content: flex-end;
  }

  .item8{
    display: flex;
    justify-content: flex-end;
  }

  .item9{
    margin-bottom: 60px;
  }
  .width-desktop{
    width: 439px;
  }

}

.foto-personal {
  margin: auto;
  margin-top: 30px;
}

@media all and (min-width:768px) {
  .foto-personal {
    margin: 0;
    margin-top: 0;
  }
}

.img-foto-pequeña {
  display: block;
  margin: auto;
  border: 4px solid #ccc;
  border-radius: 16px;
  width: 180px;
}

@media all and (min-width:768px) {
  .img-foto-pequeña {
    width: 205px;
    margin-right: 30px;
    margin-left: 30px;
  }
}

@media all and (min-width:1200px){

  .img-foto-pequeña {
    width: 278px;
    margin-right: 45px;
    margin-left: 45px;
  }

}

.nombre-apellidos {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  margin-top: 15px;
}

@media all and (min-width:768px) {
  .alineacion-izquierda {
    text-align: left;
  }
  .alineacion-derecha {
    text-align: right;
  }

}


.texto-somos {
  text-align: center;
  color: #54585A;
  line-height: 24px;
  font-family: 'Open Sans', sans-serif;
}

@media all and (min-width:768px){


  .alineacion-izquierda {
    text-align: left;
  }
  .alineacion-derecha {
    text-align: right;
  }
}

.iconos-redes-sociales {
  margin: 15px 0 45px 0;
  display: flex;
  justify-content: center;
}

@media all and (min-width:768px) {


  .iconos-redes-sociales-izquierda {
    justify-content: flex-start;
    margin: 15px 0 60px 0;
  }
  .iconos-redes-sociales-derecha {
    justify-content: flex-end;
    margin: 15px 0 60px 0;
  }
}

.lista-redes-sociales {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.icono-punto {
  position: absolute;
  right: 0;
  bottom: 50%;
  transform: translateY(35%);
  font-size: 20px;
}

.twitter,
.linkedin,
.git,
.email {
  padding: 10px 10px;
  position: relative;
}

@media all and (min-width:768px) {
  .twitter,
  .linkedin,
  .git,
  .email {
    padding: 0 10px 0 0px;
  }
}


.iconos {
  margin: 0 5px 0 5px;
  color: #099D8D;
}
